import React, { useState, useEffect } from "react";
import { inject, observer } from "mobx-react";
import { formatAddress, eqInNumber } from "../../utils/utils";
import { useAlert } from "react-alert";
import DeriNumberFormat from "../../utils/DeriNumberFormat";
import Button from "../../components/Button/Button";
import timeLogo from "./img/time.svg";
import "./compensation.less";
import useContract from "../../hooks/useContract";
import {
  EVENT_TRANS_BEGIN,
  EVENT_TRANS_END,
} from "../../utils/Constants";
import Emitter from "../../utils/Emitter";
import { BigNumber } from "ethers"

export interface Props {
  wallet: any;
  lang: any;
}

function Compensation({ wallet, lang }: Props) {
  const [balance, setBalance] = useState(0);
  const [disabled, setDisabled] = useState(true);
  const alert = useAlert();
  const [isExist, setIsExist] = useState(false);
  const contract = useContract("compensation");
  const balanceContract = useContract("ERC20USDC");
  const transactionTitle = {
    processing: "Claim Processing",
    success: "Claim Reward Executed",
    error: "Claim Reward Failed",
  };
  const transactionContent = {
    success: "Claim Reward",
    error: "Transaction Failed",
  };

  const getBalance = async () => {
    if (
      wallet.isConnected() &&
      contract &&
      eqInNumber(wallet.detail.chainId, 42161)
    ) {
      const balances = await contract.compensations(wallet.detail.account);
      let decimals = await balanceContract.decimals();
      console.log(BigNumber.from(balances._hex).toString());
      let amount = BigNumber.from(balances._hex).toNumber() / 10 ** decimals;
      if (+amount > 0) {
        setIsExist(true);
      } else {
        setIsExist(false);
      }
      setBalance(amount);
    }
  };

  const getWalletBalance = async () => {
    if (+wallet.detail.chainId === 42161 && balanceContract) {
      let balance = await balanceContract.balanceOf(wallet.detail.account);
      let decimals = await balanceContract.decimals();
      balance = BigNumber.from(balance._hex).toNumber() / 10 ** decimals;
      setBalance(balance);
    }
  };
  const click = async () => {
    if (wallet.isConnected() && contract && wallet.detail.chainId === 42161) {
      let res;
      try {
        res = await contract.claim();
      } catch (error) {
        console.log(error);
        if (error.error && error.error.data && error.error.data.message) {
          let message = error.error.data.message;
          alert.error(message);
        }
        return false;
      }
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: transactionTitle,
          content: transactionContent,
          hash: res.hash,
        });
      } else {
        return false;
      }
      const receipt = await res.wait();
      console.log("receipt", receipt);
      if (receipt) {
        console.log("receipt", receipt);
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash;
          if (receipt.status) {
            getBalance();
            // getWalletBalance()
            setDisabled(true);
            Emitter.emit(EVENT_TRANS_END, {
              title: transactionTitle,
              content: transactionContent,
              context: { success: true, hash: hash, error: "error" },
            });
          } else {
            Emitter.emit(EVENT_TRANS_END, {
              title: transactionTitle,
              content: transactionContent,
              context: { success: false, hash: hash, error: "error" },
            });
          }
        }
      }
    }
  };

  useEffect(() => {
    if (wallet.isConnected()) {
      getBalance();
      // getWalletBalance()
    }
  }, [wallet.chainId, wallet.account]);

  useEffect(() => {
    if (isExist) {
      let timestamp = new Date();
      if (timestamp.getTime() >= 1675814400000) {
        setDisabled(true);
      } else {
        setDisabled(false);
      }
    } else {
      setDisabled(true);
    }
  }, [isExist]);

  return (
    <div className="airdrop-box">
      <div className="airdrop-info">
        <div className="airdrop-info-box">
          <div className="airdrop-title">
            {/* {lang['title']} */}
            DERI Compensation Plan
          </div>
          <div>
            A fund has been set up by the treasury to compensate for the loss of
            liquidity providers due to exploit on 2022-10-28. Refer{" "}
            <a
              rel="noreferrer"
              target="_blank"
              href="https://deri-protocol.medium.com/post-mortem-of-the-2022-10-28-attack-on-deri-protocol-3eb4f1ecd49d"
            >
              here
            </a>{" "}
            for more details.
          </div>
          <div className="wallet-address">
            <span className="blue-logo"></span>
            {formatAddress(wallet.detail.account)}
          </div>
          <div className="wallet-balance">
            Recovery Amount :{" "}
            <DeriNumberFormat
              value={balance}
              decimalScale={2}
              thousandSeparator={true}
            />{" "}
            USDC
          </div>

          <div className="airdrop-time position">
            <div className="time-logo">
              <img src={timeLogo} alt="" />
            </div>
            <div className="time-text">
              <div className="time-title">
                <div>{lang["start-time"]}</div>
                <div>{lang["expire-time"]}</div>
              </div>
              <div>
                <div>2022-11-08 00:00:00 UTC</div>
                <div>2023-02-08 00:00:00 UTC</div>
              </div>
            </div>
          </div>

          <div className="claim">
            <Button
              fontWeight="bold"
              disabled={disabled}
              label={lang["claim"]}
              width={277}
              height={72}
              fontSize={32}
              onClick={click}
              bgColor={undefined}
              selectedBorderColor={undefined}
              position={undefined}
              icon={undefined}
              className={undefined}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
export default inject("wallet", "poolManager")(observer(Compensation));
